/**
 * Name : ImgComponent
 * Desc : Img  
 * Property
 *  - class :  
 *  - src :  
 **/

const ImgComponent = (props) => {
  return (
    <img
      className={props.class}
      src={process.env.PUBLIC_URL + "/images/" + props.src}
      alt={"img"}
      onClick={props.onClick}
    />
  );
};

export default ImgComponent;
